export function concatEvents(events) {
  if (!events || events.length <= 0) return () => {};
  const eventsNotNull = events.filter(a => !!a);
  return (args) => {
    for (let i = 0; i < eventsNotNull.length; i ++) {
      const event = eventsNotNull[i];
      if (event) event(args);
    }
  };
}

export function isnull(value, defaultValue) {
  if (value === 0 || value === false || value === '') return value;

  return value || defaultValue;
}

export function exists(value) {
  return value === 0 || value === false || value === '' || value;
}


