import React, {Component} from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import TextField from './TextField';
import DateIcon from './img/DateIcon';
import {datetimeObjectFromText, datetimeToObject, datetimeMask, datetimeFromText, datetimeToText, datetimeCurrentToObject, compareDateObjects} from '../utils/date';
import {concatEvents} from '../utils/func';
import './DateField.sass';

class DateField extends React.Component {
  state = {
    value: '',
    isChange: false,
    error: false
  };

  handlerOnChange = (v) => {
    const {ignoreTime, ignoreDay, ignoreMonth, onInput} = this.props;
    const {value: oldV} = this.state;
    const mv = datetimeMask(v, oldV, ignoreTime, ignoreDay, ignoreMonth);
    this.setState({value: mv, isChange: true, error: false});
    if (onInput) onInput(mv);
  };

  handlerOnBlur = () => {
    let {value, isChange} = this.state;
    const {onChange, ignoreTime, ignoreDay, ignoreMonth, gteCurrentDate, lteCurrentDate} = this.props;

    if (value && ignoreTime && ignoreDay && ignoreMonth) {
      const v = parseInt(value);
      if (v < 50) value = (v + 2000).toString();
      else if (v > 50 && v < 100) value = (v + 1000).toString();
    }

    if (onChange && isChange) {
      let formattedDate = datetimeFromText(value, ignoreTime, ignoreDay, ignoreMonth);
      let objectDate = datetimeToObject(formattedDate);
      if (isChange && value && (!objectDate || !objectDate.year) && formattedDate) {
        this.setState({error: true});
        return;
      }

      if (isChange && value && lteCurrentDate) {
        let n1 = datetimeCurrentToObject();
        if (compareDateObjects(objectDate, n1) < 0) {
          this.setState({error: true});
          return;
        }
      }

      if (isChange && value && gteCurrentDate) {
        let n = datetimeCurrentToObject();
        if (compareDateObjects(objectDate, n) > 0) {
          this.setState({error: true});
          return;
        }
      }

      onChange(formattedDate);
      this.setState({isChange: false});
    }
  };

  componentWillReceiveProps(nextProps) {
    const {value: newValue} = nextProps;
    const {value: oldValue} = this.props;
    const {error} = this.state;

    if (error) {
      if (newValue && oldValue !== newValue) {
        this.setState({error: false});
      }
    }
  }

  render() {
    let {
      onFocus, onBlur,
      value: prpValue,
      placeholder, onClickIcon,
      className: cnBox,
      defaultValue, onInput,
    } = this.props;
    prpValue = datetimeToText(prpValue);

    const {value: stValue, isChange, error} = this.state;
    const value = (!isChange && !error && prpValue) || ((isChange || error) && stValue) || defaultValue;

    return (
      <TextField
        className={cx('rootInput', cnBox)}
        classInput={cx({['dfInputError']: error})}
        value={value}
        placeholder={placeholder}
        onChange={this.handlerOnChange}
        icon={<DateIcon className={cx('rootIcon')} onClick={concatEvents([onClickIcon])} />}
        onBlur={concatEvents([this.handlerOnBlur, onBlur])}
        onFocus={onFocus}
        onInput={onInput}
      />
    );
  }
}

DateField.defaultProps = {
  ignoreTime: false,
  ignoreDay: false,
  ignoreMonth: false,
  gteCurrentDate: false,
  lteCurrentDate: false,
};

DateField.propTypes = {
  className: PropTypes.string,
  value: PropTypes.string,
  defaultValue: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func,
  onInput: PropTypes.func,
  onClickIcon: PropTypes.func,
  ignoreTime: PropTypes.bool,
  ignoreDay: PropTypes.bool,
  ignoreMonth: PropTypes.bool,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  gteCurrentDate: PropTypes.bool,
  lteCurrentDate: PropTypes.bool,
};

export default DateField;


