import React, {Component} from 'react';
import './Time.sass';
import PropTypes from 'prop-types';
import TimeIcon from './img/TimeIcon';
import MaskedInput from './MaskedInput/MaskedInput';
import cx from 'classnames';
import MaskInput from 'react-text-mask';

import {timeMask} from '../utils/date';
/*import InputMask from 'react-input-mask';
import MaskInput from 'react-text-mask';*/


class Time extends React.Component {
  state = {
    value: ''
  };

  componentWillMount() {
    const {defaultValue} = this.props;
    this.setState({value: defaultValue});
  }

  handlerOnInput = (e) => {
    const {onChange} = this.props;
    const value = timeMask(e.target.value);
    this.setState({value});
    if (onChange) onChange(value);
  };



  render() {
    const {value: sttValue} = this.state;
    const {
      onSave,
      className: cnBox,
      value = sttValue,
      placeholder,
      disableSave,
      error,
      onClickIcon,
    } = this.props;
    return (
      <div className={cx('tmBox', cnBox)}>
        <div className={cx('tmInputBox')}>
          <MaskedInput
            className={cx('tmInput', {['inputError']: error})}
            placeholderChar={' '}
            mask={[/[0-9]/, /[0-9]?/, ':', /[0-9]/, /[0-9]?/]}
            guide={false}
            keepCharPositions={true}
            placeholder={placeholder}
            value={value}
            onChange={this.handlerOnInput}
          />
          <TimeIcon
            className={cx('tmInputIconBox', {['btmIcon']: !!onClickIcon}, 'tmInputIcon')}
            onClick={onClickIcon}
          />
        </div>
        {onSave && <div className={cx('tmButton', {['tmButtonDisable']: disableSave})} onClick={disableSave || onSave}>Применить</div>}
      </div>
    );
  }
}

Time.defaultProps = {
  placeholder: '00:00',
};

Time.propTypes = {
  value: PropTypes.string,
  defaultValue: PropTypes.string,
  onSave: PropTypes.func,
  onChange: PropTypes.func,
  className: PropTypes.string,
  placeholder: PropTypes.string,
  disableSave: PropTypes.bool,
  error: PropTypes.bool,
  onClickIcon: PropTypes.func,
};

export default Time;


