import React, {Component} from 'react';
import cx from 'classnames';
import Months from '../common/Months';
import './OtcDateMonth.sass';
import PropTypes from 'prop-types';
import {datetimeCurrentToObject, datetimeToText, datetimeToObject, datetimeFromObject, constants} from '../utils/date';
import DateField from '../common/DateField';

class OtcDateMonth extends React.Component {
  state = {
    open: false,
    value: ''
  };

  handlerInputOnChange = (value) => {
    const vl = value;

    this.setState({open: false, value: vl});
    const {onChange} = this.props;
    if (onChange) onChange(vl);
  };

  componentWillMount() {
    const {defaultValue} = this.props;
    this.setState({value: defaultValue});
  }

  render() {
    const {placeholder} = this.props;
    const {value, open} = this.state;

    const {year: cYear} = datetimeCurrentToObject();
    const {month, year = cYear} = datetimeToObject(value);

    return (
      <div>
        <DateField
          onChange={(v) => this.handlerInputOnChange(v)}
          placeholder={placeholder}
          value={value}
          onClickIcon={() => this.setState({open: !open})}
          onFocus={() => this.setState({open: true})}
          onInput={() => {this.setState({open: false});}}
          ignoreTime={true}
          ignoreDay={true}
        />
        {open &&
        <Months
          className={'rootCalendar'}
          onChange={(y, m) => {this.setState({open: false, value: `${y}-${m}`});}}
          year={year}
          month={month}
          months={constants.ru.months}
        />
        }
      </div>
    );
  }
}

OtcDateMonth.defaultProps = {
};

OtcDateMonth.propTypes = {
  placeholder: PropTypes.string,
  defaultValue: PropTypes.string,
  onChange: PropTypes.func,
};

OtcDateMonth.descriptionProps = {
  placeholder: 'подсказка отображаемое в поле для ввода, если оно пустое',
  defaultValue: 'значение по умолчанию, отображается если пользователь не менял значение поля',
  onChange: 'функция вызываемая при изменении значения поля',
};

export default OtcDateMonth;


