export function concat(seporate, items) {
  if (!items) return '';

  let value = '';

  let any = false;
  for (let i = 0; i < items.length; i++) {
    const item = items[i];
    if (!item) continue;

    if (!any) {
      value += item;
      any = true;
    }
    else {
      value += seporate + item;
    }

    any = true;
  }

  return value;
}

export function to00(number) {
  number = parseInt(number);
  if (number <= 9)
    return `0${number}`;

  if (number === 0)
    return `0${number}`;

  if (!number)
    return '';

  return number.toString();
}

export function splitBy(value, separates) {
  if (!value && value !== 0) return [];

  let array = [value];
  for (let i = 0; i < separates.length; i++) {
    const sp = separates[i];
    let ar = [];
    for (let j = 0; j < array.length; j ++) {
      const items = array[j].split(sp);
      for (let z = 0; z < items.length; z++) {
        ar.push(items[z]);
      }
    }

    array = ar;
  }

  return array;
}

export function onlyNumbers(value) {
  if (value !== 0 && !value) return value;
  return value.replace(/[^-0-9]/g, '');
}

export function maxValue(value, max, maxLength) {
  if (value !== 0 && !value) return '';
  if (value.length > maxLength) {
    const vl = value.substring(0, maxLength);
    if (parseInt(vl) > max) return max;
    else return vl;
  }


  const v = parseInt(value);
  if (v > max) return max;

  return value;
}

export function concatTitle(title, value, ending) {
  if (value) {
    if (ending) {
      return concatEnding(title + value, ending);
    } else {
      return title + value;
    }
  }


  return '';
}

export function concatEnding(value, ending, where) {
  if (value || value === 0) {
    if (where) {
      if (where(value)) {
        return value + ending;
      } else {
        return value;
      }
    } else {
      return value + ending;
    }
  }

  return '';
}


