import React, {Component} from 'react';
import cx from 'classnames';
import Years from '../common/Years';
import './OtcDateYear.sass';
import PropTypes from 'prop-types';
import {datetimeCurrentToObject, constants, datetimeToObject, datetimeFromObject} from '../utils/date';
import DateField from '../common/DateField';

class OtcDateYear extends React.Component {
  state = {
    open: false,
    value: ''
  };

  handlerInputOnChange = (value) => {
    const vl = value;
    this.setState({open: false, value: vl});
    const {onChange} = this.props;
    if (onChange) onChange(vl);
  };

  componentWillMount() {
    const {defaultValue} = this.props;
    this.setState({value: defaultValue});
  }

  render() {
    const {placeholder} = this.props;
    const {value, open} = this.state;

    const {year: cYear} = datetimeCurrentToObject();
    const {year: showYear} = datetimeToObject(value);
    const year = showYear || cYear;

    return (
      <div>
        <DateField
          onChange={(v) => this.handlerInputOnChange(v)}
          placeholder={placeholder}
          value={value}
          onClickIcon={() => this.setState({open: !open})}
          onFocus={() => this.setState({open: true})}
          ignoreTime={true}
          ignoreDay={true}
          ignoreMonth={true}
          onInput={() => {this.setState({open: false});}}
        />
        {open &&
        <Years
          className={'rootCalendar'}
          onChange={(y) => {this.setState({open: false, value: `${y}`});}}
          year={year}
          selectedYear={showYear}
        />
        }
      </div>
    );
  }
}

OtcDateYear.defaultProps = {
  lg: constants.lg.ru,
};

OtcDateYear.propTypes = {
  placeholder: PropTypes.string,
  defaultValue: PropTypes.string,
  onChange: PropTypes.func,
  lg: PropTypes.string,
};

OtcDateYear.descriptionProps = {
  placeholder: 'подсказка отображаемое в поле для ввода, если оно пустое',
  defaultValue: 'значение по умолчанию, отображается если пользователь не менял значение поля',
  onChange: 'функция вызываемая при изменении значения поля',
};

export default OtcDateYear;


