import React, {Component} from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import CalendarTemplate from './CalendarTemplate';
import Days from './Days';
import Time from './Time';
import './Calendar.sass';
import {timeFromObject, timeToObject, datetimeFromObject, datetimeToObject, datetimeCurrentToObject} from '../utils/date';
import {exists} from '../utils/func';


class Calendar extends React.Component {
  static showType = CalendarTemplate.showType;

  state = {
    year: 0,
    month: 0,
    day: 0,
    time: '',
    showType: Calendar.showType.calendar,
    isChange: false,
    timeError: false,
  };

  componentWillMount() {
    const {value} = this.props;

    const {year: cYear, month: cMonth} = datetimeCurrentToObject();
    const {year = cYear, month = cMonth, time = {}} = datetimeToObject(value);
    this.setState({year, month, time});
  }

  handlerOnChange = (data, time = {}) => {
    const {onChange, timeIsRequired} = this.props;
    this.setState({...data, time, isChange: true});

    if (timeIsRequired && !exists(time.hours) && !exists(time.minutes)) {
      this.setState({timeError: true});
      return;
    }

    if (onChange) {
      if (exists(time.minutes) && !exists(time.hours)) {
        time.hours = 0;
      } else if (exists(time.hours) && !exists(time.minutes)) {
        time.minutes = 0;
      }

      onChange(datetimeFromObject({...data, time: time}));

      this.setState({isChange: false});
    }
  };

  setValue = (v) => {
    if (v.time) {
      this.setState({timeError: false});
    }

    this.setState(v);
  };

  render() {
    let {year: showYear, month: showMonth, showType, time, isChange, timeError} = this.state;
    let {value, showTime, className: cnBox, bottomCmp, lteCurrentDate, gteCurrentDate} = this.props;

    let {year, month, day} = datetimeToObject(value);
    if (isChange) {
      year = this.state.year;
      month = this.state.month;
      day = this.state.day;
    }

    return (
      <CalendarTemplate
        className={cnBox}
        onChange={this.setValue}
        showType={showType}
        year={showYear}
        month={showMonth}
        time={time}
      >
        <Days
          type={Days.type.day}
          onChange={(v) => this.handlerOnChange(v, time)}
          year={showYear}
          month={showMonth}
          day={year === showYear && month === showMonth ? day : 0}
          lteCurrentDate={lteCurrentDate}
          gteCurrentDate={gteCurrentDate}
        />
        {showTime &&
        <Time
          onClickIcon={() => this.setState({showType: Calendar.showType.time})}
          className={'crTimeField'}
          defaultValue={timeFromObject(time)}
          onChange={(t) => {this.setState({time: timeToObject(t), timeError: false});}}
          onSave={() => this.handlerOnChange({day, year, month}, time)}
          disableSave={!day}
          error={timeError}
        />}
        {bottomCmp}
      </CalendarTemplate>
    );
  }
}

Calendar.propTypes = {
  value: PropTypes.string,
  className: PropTypes.string,
  bottomCmp: PropTypes.element,
  showTime: PropTypes.bool,
  timeIsRequired: PropTypes.bool,
  gteCurrentDate: PropTypes.bool,
  lteCurrentDate: PropTypes.bool,
};

export default Calendar;


