import React, {Component} from 'react';
import cx from 'classnames';
import NextBack from './NextBack';
import './Months.sass';
import PropTypes from 'prop-types';
import {constants} from '../utils/date';

class Months extends React.Component {
  state = {
    year: -1
  };


  render() {
    const {months, month: month, onChange, className: cnBox, year: prpYear, onClickYear} = this.props;
    let {year} = this.state;
    if (year === -1) year = prpYear;

    const renderingMonths = (
      months.map((a, i) => (
        <div
          key={a}
          className={cx('monthsCell', 'teamCalendarItem', {['monthSelected']: prpYear === year && month === i + 1})}
          onClick={() => onChange && onChange(year, i + 1)}
        >
          {a}
        </div>
      ))
    );

    const renderRow = (items, key) => {
      return (
        <div key={'months-row-' + key} className={cx('monthsRow')}>
          {items}
        </div>
      );
    };

    return (
      <div className={cx('monthsBox', cnBox)}>
        <NextBack onNext={() => this.setState({year: year + 1})} onBack={() => this.setState({year: year - 1})}><div onClick={onClickYear}>{year}</div></NextBack>
        <div className={'monthsTable'}>
          {renderRow([renderingMonths[0], renderingMonths[1], renderingMonths[2]], 0)}
          {renderRow([renderingMonths[3], renderingMonths[4], renderingMonths[5]], 1)}
          {renderRow([renderingMonths[6], renderingMonths[7], renderingMonths[8]], 2)}
          {renderRow([renderingMonths[9], renderingMonths[10], renderingMonths[11]], 3)}
        </div>
      </div>
    );
  }
}

Months.defaultProps = {
  lg: constants.lg.ru
};

Months.propTypes = {
  year: PropTypes.number,
  month: PropTypes.number,
  months: PropTypes.arrayOf(PropTypes.string),
  onChange: PropTypes.func,
  className: PropTypes.string,
  onClickYear: PropTypes.func,
};

export default Months;


