import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import Icon from './Icon';
import svg from './svg/baseline-keyboard_arrow_left-24px.svg';

class LeftIcon extends React.Component {
  render () {
    const p = {...this.props, className: cx('px14', this.props.className)};
    return (<div className={cx('px24', 'center', 'left5px')}><Icon icon={svg} {...p} /></div>);
  }
}

LeftIcon.porpTypes = {
  children: PropTypes.any,
  className: PropTypes.string,
};

export default LeftIcon;


