import {Component} from 'preact-compat';
import OtcDate from '../OtcDate/OtcDate';
import OtcDateRange from '../OtcDateRange/OtcDateRange';
import OtcDateYear from '../OtcDateYear/OtcDateYear';
import PropTypes from 'prop-types'
import OtcDateMonth from '../OtcDateMonth/OtcDateMonth';
import './UiKit.sass';
import {concatTitle} from '../utils/str';

function getPropTypeName (type) {
  switch (type) {
    case PropTypes.string:
      return 'string';
    case PropTypes.number:
      return 'number';
    case PropTypes.func:
      return 'func';
    case PropTypes.bool:
      return 'bool';
  }
}

class Box extends Component {
  render() {
    const {label, children, params, name} = this.props;

    const renderParams = (prms) => {
      if (!prms) return null;

      const {propTypes = {}, defaultProps = {}, descriptionProps = {}} = prms;

      const propNames = Object.keys(propTypes);

      let lines = [];
      for (let i = 0; i < propNames.length; i++) {
        const propName = propNames[i];
        const type = getPropTypeName(propTypes[propName]);

        const typeText = concatTitle('тип: ', type, '');
        const defText = concatTitle('по умолчанию: \'', defaultProps[propName], '\'');
        const descText = concatTitle('', descriptionProps[propName]);

        lines.push((
          <div className={'boxParams'}>
            <div className={'flex5'}>
              <div className={'boxParamsPropName'}>{propName}</div>
              <div className={'boxParamsPropType'}>{typeText}</div>
              <div className={'boxParamsPropDefValue'}>{defText}</div>
            </div>
            <div className={'boxParamsPropDescription'}>{descText}</div>
          </div>
        ));
      }

      return lines.map(a => {return (<div>{a}</div>)});
    };

    return (
      <div className={'uikitBox'}>
        <div className={'uikitTitle'}>{name}:</div>
        <div className={'uikitLabel'}>
          {label}
        </div>
        <div className={'uikitTitle'}>Параметры:</div>
        <div className={'uikitParams'}>
          {renderParams(params)}
        </div>
        <div className={'uikitTitle'}>Демо:</div>
        <div className={'uikitDemo'}>
          {children}
        </div>
      </div>
    );
  }
}

Box.propTypes = {
  label: PropTypes.string,
  params: PropTypes.string,
  children: PropTypes.element,
  name: PropTypes.string
};

class UiKit extends Component {
  state = {
    otcDate: {},
    otcDateTime: {},
    otcDateRange: {},
    otcDateTimeRange: {},
    otcDateMonth: {},
    otcDateYear: {}
  };

  render() {
    const {otcDateTime, otcDate, otcDateRange, otcDateMonth, otcDateYear, otcDateTimeRange } = this.state;

    return (
      <div className={'uikit'}>
        <Box
          label='Выбрать одну дату без времени (DD.MM.YYYY), т.е. с точностью до дня'
          params={OtcDate}
          name={'OtcDate'}
        >
          <OtcDate showTime={false} onChange={(v) => this.setState({...otcDate, v})} defaultValue={otcDate.value} placeholder={otcDate.placeholder}/>
        </Box>
        <Box
          label='Выбрать одну дату со временем (DD.MM.YYYY hh24:mi), т.е. с точностью до минуты.'
          params={OtcDate}
          name={'OtcDate'}
        >
          <OtcDate showTime={true} onChange={(v) => this.setState({...otcDateTime, v})} defaultValue={otcDateTime.value} placeholder={otcDateTime.placeholder}/>
        </Box>
        <Box
          label='Выбрать одну дату со временем (DD.MM.YYYY hh24:mi), т.е. с точностью до минуты. (время обязательно для заполнения)'
          params={OtcDate}
          name={'OtcDate'}
        >
          <OtcDate showTime={true} timeIsRequired={true} onChange={(v) => this.setState({...otcDateTime, v})} defaultValue={otcDateTime.value} placeholder={otcDateTime.placeholder}/>
        </Box>
        <Box
          label='Выбрать диапазон дат: С и ПО'
          params={OtcDateRange}
          name={'OtcDateRange'}
        >
          <OtcDateRange showTime={false} onChange={(v) => this.setState({...otcDateRange, v})} {...otcDateRange}/>
        </Box>
        <Box
          label='Выбрать диапазон дат со временем: С и ПО'
          params={OtcDateRange}
          name={'OtcDateRange'}
        >
          <OtcDateRange showTime={true} onChange={(v) => this.setState({...otcDateTimeRange, v})} {...otcDateTimeRange}/>
        </Box>
        <Box
          label='Выбрать месяц (для Плана Закупок). (MM.YYYY или MMM YYYY)'
          params={OtcDateMonth}
          name={'OtcDateMonth'}
        >
          <OtcDateMonth onChange={(v) => this.setState({...otcDateMonth, v})} {...otcDateMonth}/>
        </Box>
        <Box
          label='Выбрать год (YYYY)'
          params={OtcDateYear}
          name={'OtcDateYear'}
        >
          <OtcDateYear onChange={(v) => this.setState({...otcDateYear, v})} {...otcDateYear}/>
        </Box>
      </div>
    );
  }
}

export default UiKit;


