import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Icon from './Icon';
import svg from './svg/baseline-keyboard_arrow_right-24px.svg';
import cx from 'classnames';

class RightIcon extends React.Component {
  render () {
    const p = {...this.props, className: cx('px14', this.props.className)};
    return (<div className={cx('px24', 'right', 'right5px')}><Icon icon={svg} {...p} /></div>);
  }
}

RightIcon.porpTypes = {
  children: PropTypes.any,
  className: PropTypes.string,
  onClick: PropTypes.func,
};

export default RightIcon;


