import React, {Component} from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import './TextField.sass';

class TextField extends React.Component {
  state = {
    isChange: false
  };

  handlerOnChange = (e) => {
    const {onChange} = this.props;
    let value = e.target.value;

    this.setState({isChange: true});
    if (onChange) onChange(value);
  };

  render() {
    const {value, defaultValue, placeholder, icon, className: cnBox, onFocus, onBlur, classInput: cnInput} = this.props;
    const {isChange} = this.state;
    return (
      <div className={cx('textfieldBox', cnBox)}>
        <input
          className={cx('textfieldInput', cnInput)}
          type='text'
          value={value || (!isChange && defaultValue) || ''}
          placeholder={placeholder}
          onInput={this.handlerOnChange}
          onBlur={onBlur}
          onFocus={onFocus}
        />
        <div className={'textfieldIconBox'}>
          {icon}
        </div>
      </div>
    );
  }
}

TextField.propTypes = {
  className: PropTypes.string,
  classInput: PropTypes.string,
  value: PropTypes.string,
  defaultValue: PropTypes.string,
  placeholder: PropTypes.string,
  icon: PropTypes.element,
  onChange: PropTypes.func,
  onBlur: PropTypes.func,
  onFocus: PropTypes.func,
};

export default TextField;


