import React, {Component} from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import Days from './Days';
import Time from './Time';
import './Calendar.sass';

import CalendarTemplate from './CalendarTemplate';
import {
  constants,
  timeFromObject,
  timeToObject,
  datetimeRangeToObject,
  datetimeRangeFromObject,
  datetimeCurrentToObject
} from '../utils/date';
import {exists} from '../utils/func';

class CalendarRange extends React.Component {
  static showType = CalendarTemplate.showType;

  state = {
    year: 0,
    month: 0,
    time: '',
    timeError: false,
    isChange: false,
    showType: CalendarRange.showType.calendar,
    from: {},
    to: {},
    lastChange: ''
  };

  componentWillMount() {
    const {value, changeType} = this.props;
    const range = datetimeRangeToObject(value);
    this.setState(this.getShowData({range, changeType}));
  }

  getShowData({range, changeType}) {
    const {year: cYear, month: cMonth} = datetimeCurrentToObject();

    let year;
    let month;
    if (changeType === constants.rangeValueType.from) {
      year = range.from.year || range.to.year || cYear;
      month = range.from.month || range.to.month || cMonth;
    } else if (changeType === constants.rangeValueType.to) {
      year = range.to.year || range.from.year || cYear;
      month = range.to.month || range.from.month || cMonth;
    }

    const time = changeType === constants.rangeValueType.from ? range.from.time : range.to.time;

    return {year, month, time, lastChange: changeType};
  }


  componentWillReceiveProps(nextProp) {
    const {value, changeType: newChangeType} = nextProp;
    const {changeType: oldChangeType} = this.props;

    if (newChangeType !== oldChangeType) {
      const range = datetimeRangeToObject(value);
      this.setState(this.getShowData({range, changeType: newChangeType}));
    }
  }

  handlerOnChange = (data, time = {}) => {
    const {onChange, changeType} = this.props;
    this.setState({...data, time, isChange: true});

    if (onChange) {
      if (exists(time.minutes) && !exists(time.hours)) {
        time.hours = 0;
      } else if (exists(time.hours) && !exists(time.minutes)) {
        time.minutes = 0;
      }

      if (changeType === constants.rangeValueType.from) {
        data.from.time = time;
      } else if (changeType === constants.rangeValueType.to) {
        data.to.time = time;
      }

      onChange(datetimeRangeFromObject({from: data.from, to: data.to}));
      this.setState({isChange: false});
    }
  };

  render() {
    let {year: showYear, month: showMonth, showType, time, from: stateFrom, to: stateTo, lastChange, timeError} = this.state;
    let {
      value, showTime, changeType,
      className: cnBox, bottomCmp,
      gteCurrentDateFrom, lteCurrentDateFrom,
      lteCurrentDateTo, gteCurrentDateTo,
      classBox
    } = this.props;

    const {from = stateFrom, to = stateTo} = datetimeRangeToObject(value);

    return (
      <CalendarTemplate
        className={cx(cnBox, classBox)}
        onChange={v => this.setState(v)}
        showType={showType}
        year={showYear}
        month={showMonth}
        classBox={classBox}
      >
        <Days
          type={Days.type.range}
          onChange={(v) => this.handlerOnChange(v, time)}
          year={showYear}
          month={showMonth}
          from={from}
          to={to}
          lastChange={lastChange}
          gteCurrentDateFrom={gteCurrentDateFrom}
          lteCurrentDateFrom={lteCurrentDateFrom}
          lteCurrentDateTo={lteCurrentDateTo}
          gteCurrentDateTo={gteCurrentDateTo}
        />
        {showTime && changeType &&
        <Time
          onClickIcon={() => this.setState({showType: CalendarRange.showType.time})}
          error={timeError}
          className={cx('crTimeField')}
          defaultValue={timeFromObject(time)}
          onChange={(t) => this.setState({time: timeToObject(t), timeError: false})}
          onSave={() => this.handlerOnChange({from, to}, time)}
        />
        }
        {bottomCmp}
      </CalendarTemplate>
    );
  }
}

CalendarRange.propTypes = {
  value: PropTypes.string,
  className: PropTypes.string,
  classBox: PropTypes.string,
  showTime: PropTypes.bool,
  changeType: PropTypes.string,
  onChange: PropTypes.func,
  bottomCmp: PropTypes.element,
  gteCurrentDateFrom: PropTypes.bool,
  lteCurrentDateFrom: PropTypes.bool,
  lteCurrentDateTo: PropTypes.bool,
  gteCurrentDateTo: PropTypes.bool,
};

export default CalendarRange;


