import React, {Component} from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import DateField from './DateField';
import './DateRangeField.sass';

class DateRangeField extends React.Component {
  render() {
    const {
      valueFrom, placeholderFrom,
      valueTo, placeholderTo,
      onChange, onClickFrom,
      onClickTo, popUpFrom,
      popUpTo, ignoreTime,
      onFocusFrom, onFocusTo,
      onInput
    } = this.props;

    const renderTextField = ({cn, popUp, onClick, key, value, placeholder, icon, onChange, defaultValue, onFocus, onInput}) => {
      return (
        <div style={{width: '100%'}}>
          <DateField
            key={key}
            className={cx('rootInput', cn)}
            value={value}
            defaultValue={defaultValue}
            placeholder={placeholder}
            onChange={onChange}
            onClickIcon={onClick}
            ignoreTime={ignoreTime}
            onFocus={onFocus}
            onInput={onInput}
          />
          <div className={'popUp'}>
            {popUp}
          </div>
        </div>
      );
    };

    const elements = [
      renderTextField({
        popUp: popUpFrom,
        onClick: onClickFrom,
        key: 'rangefield-from',
        value: valueFrom,
        placeholder: placeholderFrom,
        onChange: (vl) => onChange && onChange(vl, valueTo),
        onInput: (vl) => onInput && onInput(vl, valueTo),
        onFocus: onFocusFrom
      }),

      renderTextField({
        popUp: popUpTo,
        onClick: onClickTo,
        key: 'rangefield-to',
        value: valueTo,
        placeholder: placeholderTo,
        onChange: (vl) => onChange && onChange(valueFrom, vl),
        onInput: (vl) => onInput && onInput(valueFrom, vl),
        onFocus: onFocusTo
      })
    ];

    return (
      <div className={'rootInputBox'}>
        {elements}
      </div>
    );
  }
}

DateRangeField.defaultProps = {
  ignoreTime: false
};

DateRangeField.propTypes = {
  valueFrom: PropTypes.string,
  placeholderFrom: PropTypes.string,
  valueTo: PropTypes.string,
  placeholderTo: PropTypes.string,

  popUpFrom: PropTypes.element,
  popUpTo: PropTypes.element,

  onChange: PropTypes.func,
  onInput: PropTypes.func,
  onClickFrom: PropTypes.func,
  onClickTo: PropTypes.func,
  onFocusTo: PropTypes.func,
  onFocusFrom: PropTypes.func,


  ignoreTime: PropTypes.bool,
};

export default DateRangeField;


