import React, {Component} from 'react';
import cx from 'classnames';
import NextBack from './NextBack';
import './Years.sass';
import {getMonthName, monthDown, monthUp} from '../utils/date';
import PropTypes from 'prop-types';

class CalendarNextBack extends React.Component {
  render() {
    const {year, month, onChange, onClickYear, onClickMonth} = this.props;

    return (
      <NextBack onNext={() => onChange(monthUp({year, month}))} onBack={() => onChange(monthDown({year, month}))}>
        <div className={cx('calendarLink')} onClick={onClickMonth}>{getMonthName(month)}</div>
        <div className={cx('calendarLink')} onClick={onClickYear}>{year}</div>
      </NextBack>
    );
  }
}

CalendarNextBack.propTypes = {
  year: PropTypes.number,
  month: PropTypes.number,
  onChange: PropTypes.func,
  onClickYear: PropTypes.func,
  onClickMonth: PropTypes.func,
};

export default CalendarNextBack;


