import React, {Component} from 'react';
import cx from 'classnames';
import NextBack from './NextBack';
import './Years.sass';
import PropTypes from 'prop-types';
import {getDecade} from '../utils/date';

class Years extends React.Component {
  state = {
    year: -1
  };

  render() {
    const {onChange, className: cnBox, year: prpYear, selectedYear} = this.props;
    let {year} = this.state;
    if (year === -1) year = prpYear;
    const decade = getDecade(year);

    const renderingYear = (y) => {
      return (
        <div
          className={cx('yearsCell', {['selectedYear']: selectedYear === y}, 'teamCalendarItem')}
          onClick={() => onChange && onChange(y)}
        >
          {y}
        </div>
      );
    };

    const renderRow = (y0, y1, y2, y3) => {
      return (
        <div className={'yearsRow'}>
          {y0 && renderingYear(y0)}
          {y1 && renderingYear(y1)}
          {y2 && renderingYear(y2)}
          {y3 && renderingYear(y3)}
        </div>
      );
    };

    if (year < 10) return null;

    return (
      <div className={cx('yearsBox', cnBox)}>
        <NextBack
          onNext={() => {
            if (decade + 10 > 9999) return;
            this.setState({year: decade + 10});
          }}
          onBack={() => {
            if (decade - 10 < 10) return;
            this.setState({year: decade - 10});
          }}
        >
          {decade} - {decade + 10}
        </NextBack>
        <div className={'yearsTable'}>
          {renderRow(decade, decade + 1, decade + 2, decade + 3)}
          {renderRow(decade + 4, decade + 5, decade + 6, decade + 7)}
          {renderRow(decade + 8, decade + 9, decade + 10)}
        </div>
      </div>
    );
  }
}

Years.propTypes = {
  year: PropTypes.number,
  selectedYear: PropTypes.number,
  onChange: PropTypes.func,
  className: PropTypes.string
};

export default Years;


