import React, {Component} from 'react';
import cx from 'classnames';
import './NextBack.sass';
import LeftIcon from './img/LeftIcon';
import RightIcon from './img/RightIcon';

function NextBack(props) {
  const {children, onNext, onBack, className: cnBox} = props;
  return (
    <div className={cx('nextbackBox', 'nextbackHeaderStyle', cnBox, 'teamCalendarTitle')}>
      <div onClick={onBack}><LeftIcon className={'nextbackGoLeftRightIcon'} /></div>
      <div>
        <div className={cx('nextbackHeader', 'teamCalendarTitleLocation')}>{children}</div>
      </div>
      <RightIcon onClick={onNext} className={'nextbackGoLeftRightIcon'} />
    </div>
  );
}

export default NextBack;


